@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* border: 1px solid red; */
}
body {
  font-family: "Montserrat";
}
.app-wrapper {
  background-image: url(/weather-app/static/media/Scene-24.d1b3d690.jpg);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  transition: 0.4s ease-out;
}
.app-wrapper.warm {
  background-image: url(/weather-app/static/media/Scene-7.11069942.jpg);
}
main {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.75)
  );
  padding: 2rem;
}
.search-box {
  width: 49rem;
  margin: 0 auto;
  display: flex;
  margin-bottom: 5rem;
}

.search-box button {
  border-radius: 0 0 1rem 0;
  margin-top: -2rem;
  font-family: "Montserrat" !important;
  font-size: 1rem;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  color: #313131;
  transition: 0.4s ease;
  width: 9rem;
  border: none;
  outline: none;
  background-color: rgba(70, 70, 70, 0.2);
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  text-transform: uppercase;
  color: aliceblue;
  cursor: pointer;
}
.search-box button:hover {
  background-color: rgba(42, 42, 42, 0.2);
}

@media only screen and (max-width: 600px) {
  .search-box button {
    width: 5rem;
    font-size: 0.8rem;
  }
  .search-box {
    width: 18rem;
  }
}
@media only screen and (max-width: 992px) and (min-device-width: 601px) {
  .search-box button {
    width: 6rem;
  }
  .search-box {
    width: 41rem;
  }
}
.search-box .search-input:focus {
  background-color: rgba(255, 255, 255, 0.75);
}
.location-box .location {
  color: aliceblue;
  font-size: 2rem;
  font-weight: 600;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}
.location-box .date {
  color: aliceblue;
  font-size: 1.2rem;
  font-weight: 300;
  text-align: center;
  font-style: italic;
  text-shadow: 1px 1px rgba(50, 50, 70, 0.5);
}
.weather-box {
  text-align: center;
}
.weather-box .temp {
  width: 20%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 2rem auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 1rem;
  padding: 1rem 1.5rem;
  color: aliceblue;
  font-size: 7.5rem;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}
@media only screen and (max-width: 992px) and (min-device-width: 601px) {
  .weather-box .temp {
    width: 50%;
    font-size: 7rem;
  }
}

@media only screen and (max-width: 600px) {
  .weather-box .temp {
    width: 100%;
    font-size: 7rem;
  }
}
.weather-box .weather {
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  color: aliceblue;
  font-size: 2.5rem;
  font-weight: 700;
}

.search-input {
  display: block;
  width: 40rem;
  margin: 0 auto;
  padding: 1rem;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0 0 0 1rem;
  margin-top: -2rem;
  font-family: "Montserrat" !important;
  font-size: 1rem;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  color: #313131;
  transition: 0.4s ease;
}
@media only screen and (max-width: 600px) {
  .search-input {
    width: 13rem;
  }
}
@media only screen and (max-width: 992px) and (min-device-width: 601px) {
  .search-input {
    width: 35rem;
  }
}

